.card{
  width: 100%;
  //position: relative;
  min-height: 288px;
  display: flex;
  flex-direction: column;
  &.no-index{
    .card-content{
      margin: 48px 0 56px;
      p{
        max-width: 304px;
        margin: auto;
        text-align: center;
        font:{
          size: 1.6rem;
          weight: $font-regular;
        }
        line-height: 2rem;
        letter-spacing: normal;
        color: $mid-gray--text;
        @media (max-width: 1343px){
          font-size: 1.4rem;
          line-height: 1.6rem;
        }
        @media (max-height: 671px) and (max-width: 1264px){
          font-size: 1.4rem;
          line-height: 1.6rem;
        }
      };
    }
    .card-footer{
      .btn{
        display: none;
      }
    }
  }
  &.dangerously{
    .card-header--factors_icon{
      position: absolute;
      display: block;
      cursor: pointer;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background-image: url("data:image/svg+xml;charset=utf8,%3C?xml version='1.0' encoding='UTF-8'?%3E%3Csvg width='40px' height='40px' viewBox='0 0 40 40' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3C!-- Generator: sketchtool 54.1 (76490) - https://sketchapp.com --%3E%3Ctitle%3EAC5EEC6E-2150-4A16-BBE1-27952C7829B7%3C/title%3E%3Cdesc%3ECreated with sketchtool.%3C/desc%3E%3Cg id='Symbols' stroke='none' stroke-width='1' fill='none' fill-rule='evenodd'%3E%3Cg id='icon/stopfactor/XL/stop'%3E%3Cg id='Group'%3E%3Cg id='Group-2'%3E%3Crect id='Rectangle' x='0' y='0' width='32' height='32'%3E%3C/rect%3E%3Ccircle id='Oval' fill='%23F24C3D' cx='20' cy='20' r='20'%3E%3C/circle%3E%3Cg id='Group-9' transform='translate(15.000000, 15.000000)' stroke='%23FFFFFF' stroke-linecap='square' stroke-width='3.5'%3E%3Cpath d='M2.66009437e-13,10 L10,1.71418435e-13' id='Line-7'%3E%3C/path%3E%3Cpath d='M-3.96127575e-13,2.40030218e-12 L10,10' id='Line-7'%3E%3C/path%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
      background-position: center;
      background-repeat: no-repeat;
      top: -8px;
      right: 8px;
      background-color: $white;
      z-index: 2;
      &.active{
        &:after{
          position: absolute;
          content: '';
          z-index: -1;
          left: 0;
          top: 0;
          transform:scale(1.6);
          width: 40px;
          height: 40px;
          border: 1px solid $red;
          border-radius: 50%;
          opacity: .2;
          box-sizing: border-box;
          animation: cbhCircle 1.5s infinite ease-in-out ;

        }
        &:before{
          position: absolute;
          z-index: -1;
          content: '';
          left: 0;
          top: 0;
          width: 40px;
          height: 40px;
          border-radius: 50%;
          background-color: $red;
          opacity: .3;
          animation: cbhCircle-inner 1.5s infinite ease-in-out ;
        }
      }
      &:hover {
        filter: grayscale(.1);
        .hard-hint{
         visibility: visible;
          opacity: 1;
        }
      }

      .hard-hint{
       visibility: hidden;
        opacity: 0;
        position: absolute;
        z-index: 99;
        top: 0;
        left: 100%;
        margin-left: 12px;
        margin-bottom: 24px;
        background-color: $white;
        width: 260px;
        padding: 20px;
        border-radius: 4px;
        box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.2);
        transition: opacity .6s ease;
        line-height: 1.6rem;
        &:after{
          position: absolute;
          content: '';
          top: 16px;
          left: -16px;
          width: 16px;
          height: 8px;
          box-sizing: border-box;
          border-top: 4px solid transparent;
          border-bottom: 4px solid transparent;
          border-left: 8px solid transparent;
          border-right:8px solid $white ;
        }
        &.top{
          top: auto;
          bottom: 0;
          margin-bottom: 0;
          &:after{
            top: auto;
            bottom: 16px;
          }
        }
        @media (max-width: 1344px){
          right: 0;
          left: auto;
          top: 100%;
          margin-bottom: 0;
          margin-top: 24px;

          &:after{
            top: -16px;
            width:8px;
            height:16px;
            left: auto;
            right: 16px;
            border-top: 8px solid transparent;
            border-bottom: 8px solid $white;
            border-left: 4px solid transparent;
            border-right:4px solid transparent;
          }
          &.top{
            right: 100%;
            margin-right: 16px;
            &:after{
              top: auto;
              bottom: 16px;
              left: auto;
              right: -16px;
              width:16px;
              height:8px;
              border-top: 4px solid transparent;
              border-bottom: 4px solid transparent;
              border-left: 8px solid $white;
              border-right:8px solid transparent;
            }
          }
        }
        &--header{
          display: block;
          margin-bottom: 8px;
          font-size: 1.4rem;
          font-weight: $font-bold;
          line-height: 1.6rem;
        }
        &--text{
          font:{
            size: 1.4rem;
            weight: $font-regular;
          }
          line-height: 1.6rem;
        }

      }
    }
  }
  &.satisfactorily{
    .card-header--factors_icon{
      position: absolute;
      display: block;
      cursor: pointer;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background-image: url("data:image/svg+xml;charset=utf8,%3C?xml version='1.0' encoding='UTF-8'?%3E%3Csvg width='40px' height='40px' viewBox='0 0 40 40' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3C!-- Generator: sketchtool 54.1 (76490) - https://sketchapp.com --%3E%3Ctitle%3E228AA890-4BB7-4B13-B5C6-7B04F61B2E72%3C/title%3E%3Cdesc%3ECreated with sketchtool.%3C/desc%3E%3Cg id='Page-1' stroke='none' stroke-width='1' fill='none' fill-rule='evenodd'%3E%3Cg id='CARDS/DESKTOP/GUIDES-HINT' transform='translate(-384.000000, -291.000000)'%3E%3Cg id='icon/stopfactor/XL/alert' transform='translate(384.000000, 291.000000)'%3E%3Cg id='Group'%3E%3Cg id='Group-2'%3E%3Crect id='Rectangle' x='0' y='0' width='32' height='32'%3E%3C/rect%3E%3Ccircle id='Oval' fill='%23E6A345' cx='20' cy='20' r='20'%3E%3C/circle%3E%3Cpath d='M17.6,10 L22.4,10 L21.5625,23.893918 L18.46875,23.893918 L17.6,10 Z M20.0325,31.6 C19.2741629,31.6 18.6458358,31.3483896 18.1475,30.8451613 C17.6491642,30.341933 17.4,29.721294 17.4,28.9832258 C17.4,28.2451576 17.6491642,27.63011 18.1475,27.1380645 C18.6458358,26.646019 19.2741629,26.4 20.0325,26.4 C20.7691703,26.4 21.3812476,26.646019 21.86875,27.1380645 C22.3562524,27.63011 22.6,28.2451576 22.6,28.9832258 C22.6,29.721294 22.3562524,30.341933 21.86875,30.8451613 C21.3812476,31.3483896 20.7691703,31.6 20.0325,31.6 Z' id='!' fill='%23FFFFFF' fill-rule='nonzero'%3E%3C/path%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E");      background-position: center;
      background-repeat: no-repeat;
