            size: 1.4rem;
            weight: $font-regular;
          }
          line-height: 1.6rem;
        }

      }
    }
  }
  &.main-card-eliminated_fns{
    .main-card-header--registration {
      &.warning{
        color: $red;
      }
    }
    .main-card-info:not(.full){
      .title{
        color: $red;
      }
    }

  }
  &.main-card-eliminated{
    .main-card-header--status {
      display: none;
    }
    .main-card-header--registration{
      color: $mid-gray--text;
      position: relative;
      .icon-red-question{
        circle{
          stroke: $light-gray;
        }
        path{
          fill:$mid-gray--text
        }
        &:hover{
          circle{
            fill: $mid-gray--text;
            stroke: $mid-gray;
          }
          path{
            fill:$white;
          }
        }
      }


    }
    .card-header{
      color: $mid-gray--text;
    }
  }
  &.main-card-eliminated_nalog{
    .main-card-header--registration {
      color: $red;
    }
  }
  &.double{
    .card-footer{
      .btn{
        width: 304px;
        @media (max-width: 1343px){
          width: 224px;
        }
        @media (max-height: 671px) and (max-width: 1264px){
          width: 224px;
        }
      }
    }
  }

  .lazyload{
    width: 100%;
    height: 288px;
    flex: 1 0 auto;
    margin-bottom: 160px;
    background-image: url(../images/lazyload-card-small.svg);
    background-size:100%;
    background-repeat: no-repeat;
    background-position: top left;
    animation: lazyload 2s ease-in-out infinite;
    @media (min-width: 1264px) and (min-height:671px){
      background-image: url(../images/lazyload-card-big.svg);
      margin-bottom: 152px;
    }
    @media (min-width: 1344px){
      background-image: url(../images/lazyload-card-big.svg);
      height: 296px ;
      margin-bottom: 152px;
    }

  }
  .lazyload-main{
    width: 100%;
    height:  816px;
    margin-bottom: 96px;
    flex: 1 0 auto;
    background-image: url(../images/lazyload-card-small-main.svg);
    background-size:100%;
    background-repeat: no-repeat;
    background-position: top left;
    animation: lazyload 2s ease-in-out infinite;
    @media (min-width: 1264px) and (min-height:671px){
      background-image: url(../images/lazyload-card-big-main.svg);
    }
    @media (min-width: 1344px){
      background-image: url(../images/lazyload-card-big-main.svg);
    }

  }
  .card-header--factors_icon{
    display: none;
  }


  &.child{
    .card-header{
      h2{
        width: 100%;
        max-width: none;
      }
    }
  }
  &-header{
    position: relative;
    margin-bottom: 16px;
    font:{
      weight: $font-bold;
      size: 2.2rem;
    }
    line-height: 3.2rem;
    color: $black--text;
    @media (min-width: 1264px) and (min-height:671px){
      font-size: 2.8rem;
      line-height: 4rem;
    }
    @media (min-width: 1344px){
      font-size: 2.8rem;
      line-height: 4rem;
    }

    h1,h2{
      max-width: 469px;
      width: 59.2%;
    }
    h1[data-hint]{
      &:before{
        position: absolute;
        z-index: 999;
        pointer-events: none;
        content: attr(data-hint);
        display: block;
        padding: 12px;
        opacity: 0;
        max-width: 100%;
        bottom: 100%;
        margin-bottom: 8px;
        border-radius: 4px;
        background-color: rgb(153, 153, 153);
        transition: .2s ease opacity;
        text-transform: uppercase;
        font:{
          size: 1.4rem;
          weight: $font-regular;
        }
        line-height: 1.6rem;
        letter-spacing: 0.5px;
        color: rgb(255, 255, 255);

      }
      &:after{
        position: absolute;
        z-index: 99;
        content: '';
        width: 8px;
        height: 16px;
        left: 12px;
        bottom: 100%;
        margin-bottom: -8px;
        border: 8px solid transparent;
        border-top-color:rgb(153, 153, 153);
        box-sizing: border-box;
        opacity: 0;
        transition: .2s ease opacity;
      }
      &:hover{
        &:before,
        &:after{
          opacity: 1;
        }

      }
    }

    &--factors{
      width: 280px;
      position: absolute;
      display: block;
      top: 0;
      right: 0;
      text-align: right;
      font:{
        weight: $font-bold;
        size: 2.8rem;
      }
      line-height: 4rem;

      color:$red;
      transition: .2s all ease;
      &:hover{
        text-decoration: none;
        opacity: 0.7;
        color:$red;
      }
    }
  }
  &-footer{
    width: 100%;
    position: relative;
    height: 48px;
    &-btn{
      position: relative;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;

      .btn{
        width: 100%;
        max-width: 384px;
        text-transform: none;
        letter-spacing: normal;
        span{
          color: $mid-gray--text;
        }

      }
    }

    &-link{
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 112px;
      padding: 8px;
      position: absolute;
      right: 8px;
      top: 50%;
      transform: translateY(-50%);

      a{
        display: block;
        opacity: .4;
        transition: opacity .2s ease;
        width: 32px;
        height: 32px;
        &[data-hint]{
          position: relative;
          &:before{
            position: absolute;
            z-index: 99;
            pointer-events: none;
            content: attr(data-hint);
            display: block;
            padding: 4px 8px;
            opacity: 0;
            max-width: 464px;
            top: 100%;
            left: 50%;
            margin-top: 4px;
            border-radius: 4px;
            background-color: rgb(153, 153, 153);
            transition: .2s ease opacity;
            text-transform: none;
            font:{
              family: $font-family;
              size: 1.3rem;
              weight: $font-regular;
            }
            line-height: 1.6rem;
            letter-spacing: 0.5px;
            transform: translateX(-50%);
            color: rgb(255, 255, 255);

          }
        }
        &:hover{
          text-decoration: none;
          opacity: 1;
          &:before{
            opacity: 1;
          }
        }
      }
      &--download{
        background-image: url("data:image/svg+xml;charset=utf8,%3C?xml version='1.0' encoding='UTF-8'?%3E%3Csvg width='32px' height='32px' viewBox='0 0 32 32' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3C!-- Generator: sketchtool 52.6 (67491) - http://www.bohemiancoding.com/sketch --%3E%3Ctitle%3E15C97301-E412-4403-BC48-8DB05A83377C%3C/title%3E%3Cdesc%3ECreated with sketchtool.%3C/desc%3E%3Cg id='Symbols' stroke='none' stroke-width='1' fill='none' fill-rule='evenodd'%3E%3Cg id='icon/card/download/L/normal'%3E%3Cg id='Group'%3E%3Cg id='Group-25'%3E%3Crect id='Rectangle' opacity='0.200000003' x='0' y='0' width='32' height='32'%3E%3C/rect%3E%3Crect id='Rectangle' fill='%23000000' x='9' y='21' width='14' height='2'%3E%3C/rect%3E%3Crect id='Rectangle' fill='%23000000' x='15' y='9' width='2' height='9'%3E%3C/rect%3E%3C/g%3E%3C/g%3E%3Cpolygon id='Rectangle' fill='%23000000' points='12.5 15 19.5 15 16 19.75'%3E%3C/polygon%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
        &.reloaded{
          background-image: none;
          position: relative;
          opacity: 1;
          &:after{
            content: '';
            width: 16px;
            height: 16px;
            position: absolute;
            margin-top: 8px;
            margin-left: 8px;
            box-sizing: border-box;
            border-radius: 50%;
            border: 2px solid $mid-gray;
            border-bottom-color: transparent;
            animation: lds-ring 1.2s cubic-bezier(1, 1, 1, 1) infinite;
          }
        }
      }
      &--share{
        background-image: url("data:image/svg+xml;charset=utf8,%3C?xml version='1.0' encoding='UTF-8'?%3E%3Csvg width='32px' height='32px' viewBox='0 0 32 32' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3C!-- Generator: sketchtool 52.6 (67491) - http://www.bohemiancoding.com/sketch --%3E%3Ctitle%3E5BF25B24-7353-42A0-B10A-53AF445FCF4D%3C/title%3E%3Cdesc%3ECreated with sketchtool.%3C/desc%3E%3Cg id='Symbols' stroke='none' stroke-width='1' fill='none' fill-rule='evenodd'%3E%3Cg id='icon/card/share/L/normal'%3E%3Cg id='Group'%3E%3Cg id='Group-25'%3E%3Crect id='Rectangle' opacity='0.200000003' x='0' y='0' width='32' height='32'%3E%3C/rect%3E%3Cpolygon id='Rectangle' fill='%23000000' points='18 21 18 9 25 15'%3E%3C/polygon%3E%3C/g%3E%3Cpath d='M18,12.5 C18,14.5 18,15.5 18,17.5 C14.8550239,17.5 11,19 8,22 C8,17 14.5,12.5 18,12.5 Z' id='Rectangle' fill='%23000000'%3E%3C/path%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
        &.reloaded{
          background-image: none;
          position: relative;
          opacity: 1;
          &:after{
            content: '';
            width: 16px;
            height: 16px;
            position: absolute;
            margin-top: 8px;
            margin-left: 8px;
            box-sizing: border-box;
            border-radius: 50%;
            border: 2px solid $mid-gray;
            border-bottom-color: transparent;
            animation: lds-ring 1.2s cubic-bezier(1, 1, 1, 1) infinite;
          }
        }
      }
