          margin-right: 0;
          margin-left: 0;
          padding-bottom: 16px;
          .table-row{
            padding: 6px 16px;
            border-radius: 4px;
            box-sizing: border-box;
            cursor: default;
            &:hover{
              background-color: transparent;
            }
          }
          .table-col{
            &:first-child{
              font: {
                size: 1.4em;
                weight: $font-bold;
              }
              line-height: 2rem;
              letter-spacing: normal;
              color:$black--text;
              @media (min-width: 1264px) and (min-height:671px){
                font-size: 1.6rem;
              }
              @media (min-width: 1344px){
                font-size: 1.6rem;
              }
              span{
                width: auto;
                border-bottom: 1px solid rgba(0, 0, 0, .2);
              }

            }
          }
        }
      }
    }
    //стили для карточки собственники
    .owner{
      margin-bottom: 24px;

      &-one{
        margin-bottom: 24px;
        width: 100%;
        border-radius: 8px;
        box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.12);
        background-color: rgb(255, 255, 255);
        padding: 16px 24px;
        box-sizing: border-box;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;

        &--text{
          width: calc(100% - 52px);
        }

        .icon-question{
          position: relative;
          width: 20px;
          height: 20px;
          cursor: pointer;
          circle, path{
            transition: .2s all ease;
          }
          &:hover{
            circle{
              fill:$black;
              fill-opacity: .4;
            }
            path{
              fill:#fff;
              fill-opacity: 1;
            }

            .hard-hint{
              opacity: 1;
              display: block;
            }
          }
          .hard-hint{
            display: none;
            opacity: 0;
            position: absolute;
            z-index: 99;
            right: -8px;
            top: 36px;
            padding: 20px;
            box-sizing: border-box;
            width: 304px;
            border-radius: 4px;
            background-color: rgb(255, 255, 255);
            box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.12);
            transition: .6s ease;
            font-size: 1.4rem;
            line-height: 1.6rem;
            &:after{
              position: absolute;
              content: '';
              display: block;
              right: 8px;
              width: 8px;
              height: 8px;
              bottom: 100%;
              transition: .6s ease;
              border: 8px solid transparent;
              border-bottom-color:$white;
              box-sizing: border-box;
            }
            .hard-hint--header{
              font-weight: $font-bold;
              margin-bottom: 8px;
            }
          }
        }

