.company{
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  width: 100%;
  padding-top: 88px;
  margin-bottom: 64px;

  &-sidebar{
    position: fixed;
    z-index: 1;
    top: 88px;
    height: calc(100vh - 128px);
    width:100%;
    max-width: 224px;
    margin-right: 16px;
    transition: top .5s ease-in-out;
    transition-duration: .5s;
    padding-bottom: 40px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    //todo !fix me
    //overflow-y: auto;
    //overflow-x:visible;

    &.fixed{
      top: 30px;
    }
    &.overflow-height{
      position: absolute;
      bottom: 64px;
      height: auto;
      top: auto;
    }

    &.lazyload{
      ul > li > a{
        pointer-events: none;
        opacity: .5;
        &:before{
          display: none;
        }
      }

      .company-btn{

        &--inner{
          a {
            opacity: .5;
            pointer-events: none;
          }
        }
      }
    }

    & > ul{
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      padding-top: 8px;
      @media (min-width: 1344px) {
        padding-top: 16px;
      }
      & > li{
        position: relative;
        margin-bottom: 12px;
        @media (min-width: 1344px) {
          margin-bottom: 20px;
        }
        &[data-hint]{
          position: relative;
          cursor: pointer;
          &:before{
            top: 50%;
            position: absolute;
            z-index: 99;
            pointer-events: none;
            content: attr(data-hint);
            display: block;
            padding: 12px;
            opacity: 0;
            max-width: 464px;
            left: 100%;
            margin-left: 16px;
            border-radius: 4px;
            background-color: rgb(153, 153, 153);
            transition: .2s ease opacity;
            transition-delay: 0s;
            transform: translateY(-50%);
            text-transform: none;
            white-space: nowrap;
            font:{
              size: 1.4rem;
              weight: $font-regular;
            }
            line-height: 1.6rem;
            color: rgb(255, 255, 255);

          }
          &:after{
            top: 50%;
            position: absolute;
            z-index: 99;
            content: '';
            width: 8px;
            height: 16px;
            left: 100%;
            margin-left: 4px;
            margin-bottom: -8px;
            border: 8px solid transparent;
            border-right-color:rgb(153, 153, 153);
            box-sizing: border-box;
            opacity: 0;
            transition: .1s ease opacity;
            transform: translateY(-50%);
            transition-delay: 0s;
          }
          &:hover{
            &:before,
            &:after{
              opacity: 1;
              transition-delay: .6s;
            }

          }
        }
        & > a{

          display: inline-block;
          padding-left: 21px;
          box-sizing: border-box;
          font:{
            size: 1.4rem;
            weight: $font-regular;
          }
          line-height: 1.6rem;

          text-decoration: none;
          color: $black--text;
          transform: .2s all ease;
          @media (min-width: 1344px) {
            font-size: 1.6rem;
            line-height: 2rem;
          }
          &:before{
            position: absolute;
            content: '';
            width: 10px;
            height: 10px;
            border-radius: 50%;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
          }
          &.perfectly{
            &:before{
              background-color: $green;
              opacity: .7;
            }
            &:hover{
              color: rgb(58, 166, 94);
            }
          }
          &.satisfactorily{
            &:before{
              background-color: $gold;
            }
            &:hover{
              color: rgb(230, 163, 69);
            }
          }
          &.dangerously{
            &:before{
              background-color: $red;
            }
            &:hover{
              color: rgb(242, 76, 61);
            }
          }
          &.disabled{
            pointer-events: none;
            opacity: .5;
            color: rgba(0,0,0,.9) !important;
            &:before{
              display: none;
            }
          }
        }
      }
    }
  }

  &-content{
    width: 100%;
    max-width: 704px;
    margin: 0 auto 0 240px;
    @media (min-width: 1264px) and (min-height:671px){
      max-width: 784px;
    }
    @media (min-width: 1344px){
      max-width: 784px;
    }

    &--item{
      width: 100%;
      background-color: $white;
      padding: 32px;
      box-sizing: border-box;
      margin-bottom: 24px;
      
      &.dangerously{
        background-color: rgb(255, 232, 230);

        .btn{
          background-color: rgb(255, 232, 230);
          &:hover{
            background-color:darken(rgb(255, 232, 230), 5%);
          }
        }

      }
      &.satisfactorily{
        background-color: rgb(255, 241, 222);
        
        .card-header--factors{
          color: $gold;
        }

        .btn{
          background-color:rgb(255, 241, 214);
          &:hover{
            background-color:darken(rgb(255, 241, 214), 5%);
          }
        }

      }
      &.card-egrul{
        padding: 32px 16px;
        .card-header{
          padding: 0 16px;
        }
      }
    }
  }

  &-btn{
    display: flex;
    flex-direction: column;
    width: 100%;
    &--inner{
      margin-bottom: 16px;
      position: relative;
    }

    &--dropdown{
      position: absolute;
      display: none;
      width: 204px;
      left: 0;
      bottom: calc(100% + 16px);
      background-color: $white;
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.16);
      border-right: 4px;
      padding: 8px 0;
      box-sizing: content-box;
      &:after{
        content: '';
        position: absolute;
        bottom: -16px;
        left: 68px;
        width: 8px;
        height: 8px;
        border: 8px solid transparent;
        border-top-color: $white;
        box-sizing: border-box;

      }

      li{
        width: 100%;
        a{
          padding: 8px 16px;
          display: block;
          font:{
            size: 1.4rem;
            weight: $font-regular;
          }
          line-height: 1.6rem;
          letter-spacing: normal;
          color:$black--text;
          transition: .5s ease all;
          &:hover{
            text-decoration: none;
            background-color:  rgba(0, 0, 0, 0.05);
          }
        }
      }
    }
    .btn{
      width: 144px;
      height: 32px;
      border-radius: 16px;
      background-color: rgb(153, 153, 153);
      padding: 8px 16px;

      text-align: center;
      font:{
        size: 1.4rem;
        weight: $font-bold;
      }
      line-height: 1.6rem;
      letter-spacing: 0.5px;
      text-transform: uppercase;

      color: $white;
      &:hover{
        text-decoration: none;
        background-color: rgb(102, 102, 102);
      }
      &:active{
        transform: scale(.96);
      }
    }
  }



  &-modal{
    display: none;
    bottom: 100%;
    right: -50%;
    transform: translateX(10%);
    position: absolute;
    z-index: 99;
    border-radius: 4px;
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.16);
    background-color: rgb(255, 255, 255);
    &.share-modal{
      height: 120px;
      width: 304px;
      padding: 26px 24px 24px;
      box-sizing: border-box;

      input{
        margin-bottom: 16px;
        font:{
          size: 1.4rem;
          weight:$font-regular;
        }
        line-height: 1.6rem;
        letter-spacing: normal;
        color:$black--text;
        width: 100%;
        height: 20px;
        border:none;
        background-color: rgba(0, 0, 0, 0.1);

        &::selection{
          background-color: rgba(0, 0, 0, 0.1);
        }

      }

      .btn{
        display:  block !important;
        width: 100%;
        max-width: 100%;
        padding: 10px 11px;
        background-color: $green !important;
        &:hover{
          background-color: $green-hover !important;
        }
      }

    }
    &.download-modal,
    &.print-modal{
      width: 200px;
      padding: 24px;
      transform: translateX(-25%);
      label{
        position: relative;
        display: block;
        margin-bottom: 16px;
        &:hover{
          .checkbox-info{
            &:before{
              background-color: rgba(0, 0, 0, 0.05);
            }
          }
        }
        &.check-all{
          padding-top: 12px;
          margin-bottom: 24px;
          input{
            &:checked + .checkbox-info:after {
              opacity: 1;
              width: 8px;
              height: 4px;
              left: 4px;
              top: 4px;
              border: {
                left: solid 1.8px rgba(0, 0, 0, 0.4);
                bottom: solid 1.8px rgba(0, 0, 0, 0.4);
              }
              transform: rotate(-45deg);
              transition: none;
              background-image:none;
            }
          }
          .checkbox-info{
            color:rgba(0, 0, 0, 0.4);
            &:before{
              display: none;
            }
            &:after{
              content:'';
              background-image: url("data:image/svg+xml;charset=utf8,%3C?xml version='1.0' encoding='UTF-8'?%3E%3Csvg width='24px' height='24px' viewBox='0 0 24 24' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3C!-- Generator: sketchtool 52.6 (67491) - http://www.bohemiancoding.com/sketch --%3E%3Ctitle%3ED254B955-C740-450F-8233-2BCE7A651267%3C/title%3E%3Cdesc%3ECreated with sketchtool.%3C/desc%3E%3Cg id='Symbols' stroke='none' stroke-width='1' fill='none' fill-rule='evenodd'%3E%3Cg id='icon/close/M/normal'%3E%3Cg id='Group-14'%3E%3Cg id='Group'%3E%3Crect id='Rectangle' x='0' y='0' width='24' height='24'%3E%3C/rect%3E%3Cpath d='M4.5,4.5 L19.5,19.5' id='Line-2' stroke='%23000000' stroke-width='2' stroke-linecap='square'%3E%3C/path%3E%3Cpath d='M19.5,4.5 L4.5,19.5' id='Line-2' stroke='%23000000' stroke-width='2' stroke-linecap='square'%3E%3C/path%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
              background-size: contain;
              top: 0;
              left: 0;
              opacity: .4;
              width: 16px;
              height: 16px;
              transform: rotate(0);
              border: none;
            }
          }

        }

        input{
          position: absolute;
          z-index: -1;
          opacity: 0;
          &:checked + span:after{
            opacity: 1;
          }
        }
        .checkbox-info{
          display: block;
          position: relative;
          padding-left: 28px;
          box-sizing: border-box;
          cursor: pointer;

          font:{
            size: 1.4rem;
            weight: $font-regular;
          }
          line-height: 1.6rem;
          letter-spacing: normal;
          color: $black--text;
          span{
            letter-spacing: .5px;
          }
          &:before{
            position: absolute;
            content: '';
            left: 0;
            top: 0;
            width: 16px;
            height: 16px;
            border-radius: 2px;
            border: solid 1px rgba(0, 0, 0, 0.15);
          }
          &:after{
            position: absolute;
            content: '';
            width: 8px;
            height: 4px;
            left: 4px;
            top: 4px;
            opacity: 0;
            border:{
              left:solid 1.8px rgba(0, 0, 0, 0.9);
              bottom:solid 1.8px rgba(0, 0, 0, 0.9);
            }
            transform: rotate(-45deg);
            transition: .2s opacity ease;
          }
        }
      }
      .btn{
        display: block !important;
        width: 100%;
        letter-spacing: 0.5px;
        padding: 10px;
        max-width: 152px;
        background-color: $green !important;
        &:hover{
          background-color: $green-hover !important;
        }
        &.disabled{
          background-color: rgba(0, 0, 0, 0.1) !important;
          color: rgb(255, 255, 255) !important;
        }
      }
    }

    &.print-modal{
      transform: translateX(0);
    }
  }
}