.footer {
  position: relative;
  flex: 0 0 auto;
  width: 100%;
  min-width: 944px;
  background-color: $mid-gray;
  padding: 40px 0;

  &-nav{
    width: 100%;
    margin-bottom: 88px;
    ul{
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      li{
        margin-right: 32px;
        &:last-child{
          margin-right: 0;
        }

        a{
          font:{
            size: 1.4rem;
            weight: $font-bold;
          }
          line-height: 2rem;
          letter-spacing: 0.5px;
          text-transform: uppercase;
          color: $white;
          transition: .2s;

          &:hover{
            text-decoration: none;
            color: rgba(255,255,255,.8);
          }
        }
      }
    }
  }

  &-ask{
    max-width: 408px;
    margin-bottom: 44px;
    font-size: 2.2rem;
    font-weight: $font-bold;
    line-height: 3.2rem;
    letter-spacing: normal;
    color:$white;
    a{
      font-size: 2.2rem;
      font-weight: $font-bold;
      line-height: 3.2rem;
      letter-spacing: normal;
      color:$white;
      border-bottom: 1px solid rgba(255,255,255,.2);
      transition: .2s;
      &:hover{
        text-decoration: none;
        border-bottom: 1px solid rgba(255,255,255,.5);
      }
    }
  }

  &-copywrite{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    & > div{
      width: 44.1%;

      &.left{
        font-size: 1.4rem;
        font-weight: $font-regular;
        line-height: 1.6rem;
        letter-spacing: normal;
        color: $white;

        .info{
          display: flex;
          align-items: center;
          padding-top: 8px;
          margin-bottom: 8px;
          p{
            margin-right: 24px;
            opacity: .6;
            span{
              display: inline-block;
              margin-right: 8px;
            };
            &:last-child{
              margin-right: 0;
            }
          }
        }
        .adress{
          opacity: .6;
        }
      }

      &.right{
        display: flex;
        justify-content: flex-end;
        a{
          display:flex;
          align-items: center;
          justify-content: space-between;
          padding: 8px 16px;
          width: 184px;
          height: 56px;
          border-radius: 4px;
          background-color: rgb(26, 26, 26);
          transition: .2s ease all;
          &:hover{
            text-decoration: none;
            opacity: .7;
          }
          .icon{
            width: 32px;
            height: 32px;
            display: block;
            flex-shrink: 0;
            flex-grow: 0;
            img{
              display: block;
              max-width: 100%;
              max-height: 100%;
            }
          }

          .text{
            width: 104px;
            font-size: rem(14px);
            font-weight: $font-regular;
            line-height:2rem;
            letter-spacing: normal;
            color: rgb(255, 255, 255);
            span{
              letter-spacing: 0.5px;
            }
          }
        }
      }
    }

  }
}